import styles from "./CreatePost.module.css"

import { useState } from 'react'
import { useNavigate } from "react-router-dom"
import { useAuthValue } from "../../context/AuthContext"
import { useInsertDocument } from "../../hooks/UseInsertDocument"

const CreatePost = () => {
  const [title, setTitle] = useState("");
  const [image, setImage] = useState("");
  const [body, setBody] = useState("");
  const [tags, setTags] = useState([]);
  const [formError, setFormError] = useState("");

  const navigate = useNavigate();


  const {user}= useAuthValue();

  const { insertDocument, response } = useInsertDocument("posts");

  const handleSubmit = (e) => {
    e.preventDefault();
    setFormError("");

  //validar a imagem URL

  try{
    new URL(image)
  } catch(error){
    setFormError("A imagem precisa ser uma URL.")
  }

  //Criar o array de tags
  const tagsArray = tags.split(",").map((tag) => tag.trim().toLowerCase());


  //checar todos os valores

  if (!title || !image || !tags || !body) {
    setFormError("Por favor, preencha todos os campos!");
  }



  if(formError) return;

  console.log({
    title,
    image,
    body,
    tags:tagsArray,
    uid: user.uid,
    createdBy: user.displayName,
  });

  insertDocument({
    title,
    image,
    body,
    tags:tagsArray,
    uid: user.uid,
    createdBy: user.displayName,
  });
  //redirecionar pro home page
  navigate("/");
}

  return (
    <div className={styles.create_post}>
      <h2>Criar Post</h2>
      <p>Escreva sobre o que quiser e compartilhe com seus amigos!</p>
      <form onSubmit={handleSubmit}>
        <label>
          <span>Título:</span>
          <input type="text" 
          name="title" 
          required 
          placeholder="Escreva um título para o seu post"
          onChange={(e)=>setTitle(e.target.value)}
          value={title} />
        </label>
        <label>
          <span>URL da imagem:</span>
          <input type="text" 
          name="image" 
          required 
          placeholder="Insira uma imagem para representar o seu post"
          onChange={(e)=>setImage(e.target.value)}
          value={image} />
        </label>
        <label>
          <span>Conteúdo:</span>
          <textarea name="body" 
           required
           placeholder="Descreva com mais informações sobre o seu post"
           onChange={(e)=>setBody(e.target.value)}></textarea>
         
        </label>
        <label>
          <span>Tags:</span>
          <input type="text" 
          name="tags" 
          required 
          placeholder="Insira as tags separadas por vírgulas"
          onChange={(e)=>setTags(e.target.value)}
          value={tags} />
        </label>
        
        {!response.loading && <button className="btn">Criar post!</button>}
        {response.loading && (
          <button className="btn" disabled>
            Aguarde.. .
          </button>
        )}
        {(response.error || formError) && (
          <p className="error">{response.error || formError}</p>
        )}
      </form>
    </div>
  )
}

export default CreatePost